.menuB {
    .contentWrap {
        .intro_title {
            height: 850px;
            h1 {
                padding: 226px 0 75px;
                font-size: 48px;
                text-align: center;
                font-weight: lighter;
                color: #fff;
                line-height: 1.2;
                font-family: 'NanumSquare', sans-serif;
            }
            .intro_info {
                text-align: center;
                color: #fff;
                line-height: 1.7;
                font-family: 'NanumSquare', sans-serif;
                p:first-child {
                    font-size: 38px;
                    margin: -10px 0 4px;
                }
                p:last-child {
                    font-size: 18px;
                    line-height: 1.3;
                }
            }
            .mini_nav {
                text-align: center;
                margin-top: 95px;;
                li {
                    display: inline-block;
                    width: 177px;
                    height: 37px;
                    line-height: 37px;
                    border: 1px solid #fff;
                    border-right: 0px solid #fff;
                    opacity: .7;
                    a {
                        display: block;
                        color: #fff;
                        font-family: 'Nanum Gothic', sans-serif;
                        font-size: 13px;
                        font-weight: bold;
                    }
                    &:hover {
                        background: #fff;
                        a {color: #010101; }
                        opacity: 1;
                    }
                    &:last-child {
                        border-right: 1px solid #fff;                    
                    }
                }
            }
        }
        .intro_con {
            font-family: 'NanumSquare', sans-serif;
            .menu_align {
                max-width: 1180px;
                min-height: 680px;
                margin: 0 auto;
                position: relative;
                width: 100%;
                .updown_set {
                    text-align: left;
                    vertical-align: middle;
                    color: #202023;
                    word-break: keep-all;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 100%;
                    .txtbox {
                        width: 50%;
                        &.txbxr {
                            float:right;
                        }
                        .txt_title {
                            padding-bottom: 55px;
                            margin-bottom: 50px;
                            font-size: 40px;
                            font-weight: 300;
                            line-height: 1.2;
                            position: relative;
                            &::before {
                                position: absolute;
                                bottom:0; left:0;
                                content:'';
                                width: 60px; height: 1px;
                                background: #202023;
                            }
                            &.bake { color: #fff; }
                            &.bake::before { background: #fff; }
                        }
                        .info_a {
                            font-size: 22px;
                            line-height: 1.4;
                            font-weight: bold;
                            &.bake { color: #fff; }
                        }
                        .info_b {
                            margin: 25px 0 55px;
                            color: #707070;
                            font-size: 17px;
                            line-height: 1.4;
                            &.bake { color: #fff; }
                        }
                        a {
                            display: inline-block;
                            width: 188px;
                            height: 42px;
                            line-height: 42px;
                            text-align: center;
                            border: 1px solid #202023;
                            font-size: 15px;
                            font-weight: 500;
                            letter-spacing: 2px;
                            font-family: 'Noto Sans KR', sans-serif;
                            &.bake {
                                border: 1px solid #fff;
                                color: #fff;
                            }
                        }
                    }
                }
            }
        }
    }
}