.loginB {
    .login_container {
        padding: 245px 0 200px;
        margin: 0 auto;
        max-width: 800px;
        text-align: center;
        h1 {
            margin-bottom: 70px;
        }
        .login_txt {
            font-size: 18px;
            margin-bottom: 96px;
            color: #fff;
            font-family: 'NanumSquare', sans-serif;
            line-height: 1.7;
            p:first-child {
                font-size: 38px;
                font-weight: 600;
            }
        }
        .mem_info {
            background: #fff;
            padding: 70px 150px;
            margin: 20px 0;
            line-height: 1.7;
            h2 {
                font-size: 28px;
                line-height: 1.2em;
                color: #202023;
                margin-bottom: 39px;
                font-weight: normal;
                p {
                    font-size: 36px;
                    color: #002f6c;
                    font-weight: 400;
                    padding: 11px 0 36px;
                    letter-spacing: -3px;
                    position: relative;
                    &::before {
                        position: absolute;
                        bottom:0; left: 50%;
                        transform: translateX(-50%);
                        content:''; width: 100px; height: 1px;
                        background: #002f6c;
                    }
                }
            }
            .info_plus {
                margin-bottom: 38px;
                font-size: 15px;
                font-family: 'Noto Sans KR', sans-serif;
                color: #707070;
                line-height: 1.5em;
            }
            a {
                font-family: 'Noto Sans KR', sans-serif;
                display: block;
                border: 1px solid #002f6c;
                background: #002f6c;
                font-size: 18px;
                font-weight: 600;
                color: #fff;
                border-radius: 3px;
                height: 66px;
                line-height: 66px;
            }
        }
        .login_box {
            background: #fff;
            padding: 70px 150px;
            margin: 20px 0;
            line-height: 1.7;
            .login_form {
                input {
                    height: 56px;
                    font-size: 15px;
                    width: 100%;
                    border: solid 1px #ccc;
                    border-radius: 3px;
                    padding-left: 20px;
                    box-sizing: border-box;
                    margin-bottom: 10px;
                    &::placeholder {
                        font-family: 'Noto Sans KR', sans-serif;
                    }
                }
                .email_save {
                    margin: 4px 0 16px;
                    font-family: 'Noto Sans KR', sans-serif;
                    text-align: left;
                    input {
                        font-size: 15px;
                        border: 1px solid #ccc;
                        border-radius: 3px;
                        width: 14px; height: 14px;
                        vertical-align: middle;
                        margin:0;
                    }
                    label {
                        font-size: 14px;
                        margin-left: 10px;
                        vertical-align: middle;
                    }
                }
                input[type="submit"] {
                    font-family: 'Noto Sans KR', sans-serif;
                    display: block;
                    border: 1px solid #002f6c;
                    background: #002f6c;
                    font-size: 18px;
                    font-weight: 600;
                    color: #fff;
                    border-radius: 3px;
                    height: 66px;
                    line-height: 66px;
                    margin-bottom: 10px;
                }
                .join_btn {
                    margin-top: 20px;
                    a {
                        display: inline-block;
                        width: 32%;
                        margin-right: 2%;
                        height: 42px;
                        line-height: 42px;
                        text-align: center;
                        border: solid 1px #ccd5e2;
                        font-weight: 500;
                        color: #002f6c;
                        font-family: 'Noto Sans KR', sans-serif;         
                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }
            }
        }
    }
}