.giftB {
    .contentWrap {
        .intro_title {
            height: 460px;
            h1 {
                padding: 116px 0 19px;
                font-size: 48px;
                text-align: center;
                font-weight: lighter;
                color: #202023;
                line-height: 1.2;
                font-family: 'NanumSquare', sans-serif;
            }
            .intro_info {
                text-align: center;
                font-size: 18px;
                color: #000;
                line-height: 1.3;
                font-family: 'NanumSquare', sans-serif;
                span { font-weight: 900; }
            }
            .mini_nav {
                text-align: center;
                margin-top: 40px;;
                li {
                    display: inline-block;
                    width: 177px;
                    height: 37px;
                    line-height: 37px;
                    border: 1px solid #98989e;
                    border-right: 0px solid #98989e;
                    opacity: .7;
                    a {
                        display: block;
                        color: #202023;
                        font-family: 'Nanum Gothic', sans-serif;
                        font-size: 13px;
                        font-weight: bold;
                    }
                    &:hover {
                        background: #84847c;
                        a {color: #fff; }

                    }
                    &:last-child {
                        border-right: 1px solid #98989e;                    
                    }
                }
            }
        }
        .intro_con {
            font-family: 'NanumSquare', sans-serif;
            .members_align {
                max-width: 1180px;
                min-height: 680px;
                margin: 0 auto;
                position: relative;
                width: 100%;
                .updown_set {
                    text-align: left;
                    vertical-align: middle;
                    color: #202023;
                    word-break: keep-all;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 100%;
                    .txtbox {
                        width: 50%;
                        &.txbxr {
                            float:right;
                        }
                        .txt_title {
                            padding-bottom: 55px;
                            margin-bottom: 50px;
                            font-size: 40px;
                            font-weight: 300;
                            line-height: 1.2;
                            position: relative;
                            &::before {
                                position: absolute;
                                bottom:0; left:0;
                                content:'';
                                width: 60px; height: 1px;
                                background: #202023;
                            }
                        }
                        .info_a {
                            font-size: 22px;
                            line-height: 1.4;
                            font-weight: bold;
                            margin-bottom: 30px;
                        }
                        // .info_b {
                        //     margin: 25px 0 55px;
                        //     color: #707070;
                        //     font-size: 17px;
                        //     line-height: 1.4;
                        // }
                        a {
                            display: inline-block;
                            width: 188px;
                            height: 42px;
                            line-height: 42px;
                            text-align: center;
                            border: 1px solid #202023;
                            font-size: 15px;
                            font-weight: 500;
                            letter-spacing: 2px;
                            font-family: 'Noto Sans KR', sans-serif;
                            &.btn1 {
                                width: 280px;
                            }
                        }
                    }
                }
            }
        }
    }
}