.noticeB {
    .contentWrap {
        .intro_title {
            height: 460px;
            h1 {
                padding: 116px 0 19px;
                font-size: 48px;
                text-align: center;
                font-weight: lighter;
                color: #202023;
                line-height: 1.2;
                font-family: 'NanumSquare', sans-serif;
            }
            .intro_info {
                text-align: center;
                font-size: 18px;
                color: #000;
                line-height: 1.3;
                font-family: 'NanumSquare', sans-serif;
                span { font-weight: 900; }
            }
            .mini_nav {
                text-align: center;
                margin-top: 40px;;
                li {
                    display: inline-block;
                    width: 177px;
                    height: 37px;
                    line-height: 37px;
                    border: 1px solid #98989e;
                    border-right: 0px solid #98989e;
                    opacity: .7;
                    a {
                        display: block;
                        color: #202023;
                        font-family: 'Nanum Gothic', sans-serif;
                        font-size: 13px;
                        font-weight: bold;
                    }
                    &:hover {
                        background: #84847c;
                        a {color: #fff; }

                    }
                    &:last-child {
                        border-right: 1px solid #98989e;                    
                    }
                }
            }
        }
    }
}