.curtureB {
    .contentWrap {
        .intro {
            height: 850px;
            text-align: center;
            color: #fff;
            font-family: 'NanumSquare', sans-serif;
            h1 {
                padding: 226px 0 75px;
                font-size: 48px;
            }
            .info_tt {
                margin: -10px auto 4px;
                font-size: 38px;
                letter-spacing: -2px;
                line-height: 1.7;
            }
            .info p{
                color: #b9b8b8;
                font-size: 18px;
                line-height: 1.3;
            }
            .mini_nav {
                text-align: center;
                margin-top: 40px;;
                li {
                    display: inline-block;
                    width: 177px;
                    height: 37px;
                    line-height: 37px;
                    border: 1px solid #fff;
                    border-right: 0px solid #fff;
                    opacity: .7;
                    a {
                        display: block;
                        color: #fff;
                        font-family: 'Nanum Gothic', sans-serif;
                        font-size: 13px;
                        font-weight: bold;
                    }
                    &:hover, &.on {
                        background: #fff;
                        a {color: #010101; }
                        opacity: 1;
                    }
                    &:last-child {
                        border-right: 1px solid #98989e;                    
                    }
                }
            }
        }
        .festa_content {
            .location {
                max-width: 1180px;
                text-align: right;
                margin: 0 auto;
                padding-top: 38px;
                color: #898989;
                font-size: 12px;
                font-family: 'Noto Sans KR', sans-serif;
                font-weight: 500;
                span {
                    margin-right: 8px;
                    padding-right: 15px;
                    position: relative;
                    &::before {
                        content:'>';
                        position: absolute;
                        top:0; right:0;
                    }
                }
            }
            .block_con {
                position: relative;
                padding: 70px 0;
                text-align: center;
                .con_align {
                    max-width: 1180px;
                    margin: 0 auto;
                    .festa_tt {
                        margin-bottom: 25px;
                        padding-bottom: 34px;
                        font-size: 29px;
                        color: #293996;
                        font-weight: 800;
                        font-family: 'NanumSquare', sans-serif;
                        position: relative;
                        &::before {
                            content:'';
                            position: absolute;
                            bottom:0; left: 50%;
                            transform: translateX(-50%);
                            width: 32px; height: 3px;
                            background: #b2b2b2;
                        }
                    }
                    .festa_txt {
                        margin-bottom: 57px;
                        color: #7685a5;
                        font-size: 18px;
                        line-height: 1.4;
                        font-family: 'Noto Sans KR', sans-serif;
                        font-weight: 500;
                    }
                    .festa_info {
                        li {
                            float: left;
                            width: 30%;
                            margin: 0 1.665%;
                            padding: 0 10px;
                            word-break: keep-all;
                            .festa_info_tt {
                                padding-top: 25px;
                                color: #293996;
                                font-size: 20px;
                                font-weight: 800;
                                font-family: 'NanumSquare', sans-serif;
                            }
                            .festa_info_con {
                                padding-top: 13px;
                                color: #7a7a7a;
                                font-size: 18px;
                                line-height: 1.4;
                                font-family: 'Noto Sans KR', sans-serif;
                            }
                        }
                    }
                }
            }
            .festa_bg01 {
                background: #d1e6f7;
                &::before {
                    display: block;
                    content: "";
                    width: 100%;
                    height: 205px;
                    position: relative;
                    top: -70px;
                }
                &::after {
                    display: block;
                    content: "";
                    width: 100%;
                    height: 205px;
                    position: relative;
                    bottom: -70px;
                }
            }
        }
    }
}